<template>
  <div id="landingPage">
    <HeaderNav :logo="false" :background="false" />

    <center>
      <img src="../../assets/images/logo-large.png" srcset="../../assets/images/logo-large@2x.png 2x, ../../assets/images/logo-large@3x.png 3x">
      <h1>Visualize what Bitcoin is saying</h1>
    	<form class="main" @submit.prevent="searchForText">
    		<div class="sbox">
    			<input type="text" name="searchText" v-model="searchText" v-on:input="searchText = $event.target.value" title="Explore Bitcoin" placeholder="Enter a search term or a topic" autocomplete="off" autofocus>
    		</div>

    		<div class="buttons">
    			<input value="Explore" type="submit">
    		</div>
    	</form>

    	<div class="promo">
    		<a href="/apps/1/actions" title="popular Bitcoin apps">Popular apps</a> |
    		<a href="https://oyo.cash/" title="search Bitcoin">Oyo.cash</a>
    	</div>
    </center>

    <Footer/>
  </div>
</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'SearchHome',
  data () {
    return {
      searchText: '',
      networkName: window.networkName
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Visualize what Bitcoin is saying'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Trends.cash' },
        { name: 'description', content: 'Analyzes the popularity of top mentions on the Bitcoin blockchain. The website uses graphs to compare the search volume of different queries over time', id: 'desc' },
        { name: 'twitter:title', content: 'Visualize what Bitcoin is saying' },
        { n: 'twitter:description', c: 'Analyzes the popularity of top mentions on the Bitcoin blockchain. The website uses graphs to compare the search volume of different queries over time' },
        { itemprop: 'name', content: 'Visualize what Bitcoin is saying' },
        { itemprop: 'description', content: 'Analyzes the popularity of top mentions on the Bitcoin blockchain. The website uses graphs to compare the search volume of different queries over time' },
        { property: 'og:title', content: 'Visualize what Bitcoin is saying' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer
  },
  methods: {
    searchForText () {
      if (this.searchText) {
        this.$router.push('/result/' + Base64.encode(this.searchText) + '/30')
      }
    }
  }
};
</script>

<style>
center {
    padding-top: 89px;
}

#landingPage h1 {
    color: #5f6368;
    font-family:"Google Sans Display", Heebo, Arial, sans-serif;
    font-size:40px;
    letter-spacing:normal;
    line-height:46px;
    font-weight: 400;
    padding: 13px 8px 8px 5px;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}

form.main {
    width: 100%;
    text-align: center;
    margin-top: 50px;
    padding-bottom: 10px;
}

form.main input {
    width: 503px;
    height: 34px;
    text-align: left;
    font-size: 13px;
    border: none;
    outline: none;
}

form.main div.sbox{
    margin-bottom: 10px;
    height: 38px;
    width: 528px;
    overflow: hidden;
    padding: 6px 9px 0px 16px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
    border-radius: 2px;
    align-self: center;
    display: inline-block;
}
form.main div.sbox:hover {
   box-shadow: 0 2px 7px 0 rgba(0, 0, 0, 0.16), 0 0 0 1px rgba(0, 0, 0, 0.08);
}
form.main input[type="submit"] {
    background-color: #f2f2f2;
    color: #757575;
    font-weight: bold;
    height: 36px;
    width: 142px;
    border: 1px;
    border-radius: 2px;
    margin: 11px 4px;
    text-align: center;
}
form.main input[type="submit"]:hover {
    border: 1px solid #c6c6c6;
}
form.main input[type="submit"]:focus {
    border: 1px solid #4d90fe;
}
.promo {
  padding-bottom:80px;
}
.promo a {
  padding-left:10px;
  padding-right:10px;
}

.featured-search{
  grid-column: span 6;
  font-family: arial, sans-serif;
  margin: 15px 15px 51px 15px;
  background: #ffffff;
}

@media screen and (max-width: 1024px) {
  form.main input {
      width: 403px;
  }
  form.main div.sbox{
      width: 428px;
  }
}
@media screen and (max-width: 800px) {
  #landingPage h1 {
      font-size:32px;
  }
  form.main input {
      width: 303px;
  }
  form.main div.sbox{
      width: 328px;
  }
}
</style>



