<template>
  <div>
  <h2>How to use</h2>

  <h4>Visualize single phrase</h4>
  <p>Just write the phrase you search for. For example <code><strong>bitcoin sv</strong></code> <a href="https://trends.cash/result/Yml0Y29pbiBzdg==/30">[see on Trends]</a></p>

  <h4>Visualize multiple phrases</h4>
  <p>To search for multiple multiple phrases, separate them with ','. For example, <code><strong>bsv, abc</strong></code> <a href="https://trends.cash/result/YnN2LCBhYmM=/90">[see on Trends]</a></p>

  <h4>Visualize protocols</h4>
  <p>Put <code><strong>prefix:</strong></code> in front of a protocol code. For example, to search for Memo posts, use <code><strong>prefix:6d02</strong></code> <a href="https://trends.cash/result/cHJlZml4OjZkMDI=/30">[see on Trends]</a></p>

  <h4>Visualize transactions from (sender)</h4>
  <p>Put <code><strong>from:</strong></code> in front of the sender's address. For example <code><strong>from:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx</strong></code> <a href="https://trends.cash/result/ZnJvbTogMTd5dm8yTVk2RFNYNEpGSk5XekVkYXY5NUVERTlQY1d2eA==/30">[see on Trends]</a></p>

  <h4>Visualize transactions excluding sender</h4>
  <p>Put <code><strong>notfrom:</strong></code> in front of the sender's address. For example <code><strong>notfrom:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx</strong></code> <a href="https://trends.cash/result/bm90ZnJvbTogMTd5dm8yTVk2RFNYNEpGSk5XekVkYXY5NUVERTlQY1d2eA==/30">[see on Trends]</a></p>

  <h4>Visualize transactions to (receiver)</h4>
  <p>Put <code><strong>to:</strong></code> in front of the receiver's address. For example <code><strong>to:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx</strong></code> <a href="https://trends.cash/result/dG86IDE3eXZvMk1ZNkRTWDRKRkpOV3pFZGF2OTVFREU5UGNXdng=/30">[see on Trends]</a></p>

  <h4>Visualize transactions excluding sender</h4>
  <p>Put <code><strong>notto:</strong></code> in front of the receiver's address. For example <code><strong>notto:17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx</strong></code> <a href="https://trends.cash/result/bm90dG86IDE3eXZvMk1ZNkRTWDRKRkpOV3pFZGF2OTVFREU5UGNXdng=/30">[see on Trends]</a></p>

  <h4>Visualizing h/b/s bitdb fields</h4>
  <p>Put bitdb field <code><strong>[b1, b2, ..., b15, h1, ..., h15, s1, ..., s15]:</strong></code> in front of the searched string. For example <code><strong>s1:$</strong></code> <a href="https://trends.cash/result/czE6JA==/30">[see on Trends]</a></p>
  <p>Multiple fields can be used in the visialization. For example For example <code><strong>h1:24 s2:echo s5:name</strong></code> <a href="https://trends.cash/result/aDE6MjQgczI6ZWNobyBzNTpuYW1l/30">[see on Trends]</a></p>

  <h4>Combining searches</h4>
  <p>
    You can combine the methods above as you like. Examples:<br/>
    <code><strong>prefix:6d02 satoshi</strong></code> (searches for 'satoshi' in memo posts) <a href="https://trends.cash/result/cHJlZml4OjZkMDIgc2F0b3NoaQ==/30">[see on Trends]</a><br/>
    <code><strong>prefix:6d03, prefix:6d04</strong></code> (compared memo replies and memo posts) <a href="https://trends.cash/result/cHJlZml4OjZkMDMsIHByZWZpeDo2ZDA0/30">[see on Trends]</a><br/>
    <code><strong>prefix:6d04 from: 17yvo2MY6DSX4JFJNWzEdav95EDE9PcWvx</strong></code> (visualizes likes on memo by user address) <a href="https://trends.cash/result/cHJlZml4OjZkMDQgZnJvbToxUUNCaXlmd2RqWERzSGdoQkVyNVUyS3hVcE0yQm1tSlZ0/30">[see on Trends]</a>
  </p>
  </div>
</template>

<script>
  export default {
    name: 'HowToUse',
    components: {
    }
  };
</script>

