// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
window.convertAddress = function convertAddress (address) {
  if (window.bitdbAddressFormat === 'legacy' && window.bchaddr.isCashAddress(address)) {
    return window.bchaddr.toLegacyAddress(address)
  }
  if (window.bitdbAddressFormat === 'cashaddr' && window.bchaddr.isLegacyAddress(address)) {
    return window.bchaddr.toCashAddress(address).substr(12)
  }
  return address
}

import Vue from 'vue'
import { Base64 } from 'js-base64'
import axios from 'axios'
import bchaddr from 'bchaddrjs'
import VueChartkick from 'vue-chartkick'
import VueQriously from 'vue-qriously'
import VueHead from 'vue-head'
import Chart from 'chart.js'
import protocolsFile from './json/protocols.json'
import router from './router'
import App from './App'

window.axios = axios
window.bchaddr = bchaddr
window.Base64 = Base64

// protocols
window.protocolsDefault = protocolsFile
if (!localStorage.getItem('protocols')) {
  window.protocols = window.protocolsDefault
} else {
  window.protocols = localStorage.getItem('protocols')
}
// network name
window.networkNameDefault = 'BSV'
window.networkName = window.networkNameDefault
if (localStorage.getItem('networkName')) {
  if (window.networkNameDefault === localStorage.getItem('networkName'))
  {
    localStorage.removeItem('networkName')
  } else {
    window.networkName = localStorage.getItem('networkName')
  }
}
// bitdb node
window.bitdbNodeDefault = 'https://babel.bitdb.network/q/1DHDifPvtPgKFPZMRSxmVHhiPvFmxZwbfh/'
window.bitdbBCHNodeDefault = 'https://bitdb.network/q/'
window.bitdbNode = window.bitdbNodeDefault
if (localStorage.getItem('bitdbNode')) {
  if (window.bitdbNodeDefault === localStorage.getItem('bitdbNode'))
  {
    localStorage.removeItem('bitdbNode')
  } else {
    window.bitdbNode = localStorage.getItem('bitdbNode')
  }
}
// bitdb api key
window.bitdbApiKeyDefault = 'qpl84tsdqd6yxd3hpxhj5ngr3rp0pvlweqy7p7rzfy'
window.bitdbApiKey = window.bitdbApiKeyDefault
if (localStorage.getItem('bitdbApiKey')) {
  if (window.bitdbApiKeyDefault === localStorage.getItem('bitdbApiKey'))
  {
    localStorage.removeItem('bitdbApiKey')
  } else {
    window.bitdbApiKey = localStorage.getItem('bitdbApiKey')
  }
}
// bitdb address format
window.bitdbAddressFormatDefault = 'legacy'
window.bitdbBCHAddressFormatDefault = 'cashaddr'
window.bitdbAddressFormat = window.bitdbAddressFormatDefault
if (localStorage.getItem('bitdbAddressFormat')) {
  if (window.bitdbAddressFormatDefault == localStorage.getItem('bitdbAddressFormat'))
  {
    localStorage.removeItem('bitdbAddressFormat')
  } else {
    window.bitdbAddressFormat = localStorage.getItem('bitdbAddressFormat')
  }
}

Vue.config.productionTip = false

Vue.use(VueQriously)
Vue.use(VueHead)
Vue.use(VueChartkick, {adapter: Chart})
VueChartkick.options = {
  library: {
    defaultFontColor: 'red'
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('toTitle', function (text) {
  let resultString = text.split(' ')[0]
  if (text.includes(' ')) {
    resultString = resultString + ' ' + text.substr(text.indexOf(' ') + 1).split(/[.|:|;|-|!|?|<|&]+/).slice(0, 1).join(' ')
  }
  return resultString
})



