<template>
  <div class="grid">
    <HeaderNav :search="true"/>

    <div class="s_results" v-if="loading === false">
      <p>
        <button @click="changeChart('actions')" v-bind:class="{ active: (chartToShow === 'actions') }">Performed actions</button>
        <button @click="changeChart('users')" v-bind:class="{ active: (chartToShow === 'users') }">Active users</button>
      </p>
      <p>
        <button @click="changePeriod(1)" v-bind:class="{ active: (periodInput === 1) }">1 day</button>
        <button @click="changePeriod(7)" v-bind:class="{ active: (periodInput === 7) }">7 days</button>
        <button @click="changePeriod(30)" v-bind:class="{ active: (periodInput === 30) }">30 days</button>
      </p>
      <pie-chart :data="chartData" :messages="{empty: 'No data'}"></pie-chart>
    </div>
    <div class="s_results" v-else>
      <div class="n_results">
        Loading...
      </div>
    </div>

    <div class="resultTable" v-if="loading === false">
      <div class="resultTable-container resultTable-separator">
        <div class="resultTable-title">
          {{ chartToShow }}
        </div>
      </div>
      <div class="resultTable-item resultTable-container resultTable-separator" v-for="(item, index) in chartDataFull">
        <div class="label-line-number">
          {{ index + 1 }}
        </div>
        <div class="label-text">
          <a :href="getTrendsLink(item[2])">
          {{ item[0] }}
          </a>
        </div>
        <div class="label-value">
          {{ item[1] }}
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'AppsHome',
  props: {
    period: {type: String, required: true},
    chart: {type: String, default: 'actions'}
  },
  data () {
    return {
      chartToShow: this.chart,
      itemsOnChart: 10,
      chartData: [],
      chartDataFull: [],
      periodInput: null,
      bitcomPrefixes: [],
      loading: true,
      errored: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Visualize Bitcoin Cash\'s apps ' + this.chart + ' count'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Trends.cash' },
        { name: 'description', content: 'Analyzes the popularity of top apps on Bitcoin Cash. See graphs comparing the most used apps ' + this.chart + ' count over the last ' + this.period + ' days' , id: 'desc' },
        { name: 'twitter:title', content: 'Visualize Bitcoin Cash\'s apps ' + this.chart + ' count' },
        { n: 'twitter:description', c: 'Analyzes the popularity of top apps on Bitcoin Cash. See graphs comparing the most used apps ' + this.chart + ' count over the last ' + this.period + ' days' },
        { itemprop: 'name', content: 'Visualize Bitcoin Cash\'s apps ' + this.chart + ' count' },
        { itemprop: 'description', content: 'Analyzes the popularity of top apps on Bitcoin Cash. See graphs comparing the most used apps ' + this.chart + ' count over the last ' + this.period + ' days'  },
        { property: 'og:title', content: 'Visualize Bitcoin Cash\'s apps ' + this.chart + ' count' }
      ]
    }
  },
  components: {
    HeaderNav,
    Footer
  },
  mounted () {
    this.periodInput = parseInt(this.period)
    this.populateChart()
  },
  watch: {
    periodInput: function (val) {
      this.$emit('updateHead')
    },
    chartToShow: function (val) {
      this.$emit('updateHead')
    }
  },
  methods: {
    changePeriod (period) {
      this.periodInput = parseInt(period)
      this.changeSearchRoute()
    },
    changeChart (chartToShow) {
      this.chartToShow = chartToShow
      this.changeSearchRoute()
    },
    changeSearchRoute () {
      this.$router.push('/apps/' + this.periodInput + '/' + this.chartToShow)
      this.populateChart()
    },
    populateChart () {
      this.loading = true
      this.chartData = []
      if (this.chartToShow === 'actions') {
        this.getActionsChartData()
      } else if (this.chartToShow === 'users') {
        this.getAddressesChartData()
      } else {
        this.loading = false
      }
    },
    getAddressesChartData () {
      let nowTimestamp = new Date().getTime() / 1000
      let beginTimestamp = nowTimestamp - (this.periodInput * 24 * 60 * 60)
      let endTimestamp = nowTimestamp

      let requests = []

        let queryMatch = {}
        queryMatch['$and'] = []
        queryMatch['$and'].push({'out.s1': { '$exists': true, '$ne': [] }})
        queryMatch['$and'].push({'out.s2': {'$ne': null}})
        queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

        let query = {
          'v': 3,
          'q': {
            'aggregate': [{
              '$match': queryMatch
            }, {
      				"$group": {
      					"_id": {
      						"prefix": "$out.b1",
      						"address": "$in.e.a"
      					},
      					"count": {
      						"$sum": 1
      					}
      				}
      			},
      			{
      				"$group": {
      					"_id": "$_id.prefix",
      					"count": {
      						"$sum": 1
      					}
      				}
      			}],
            'limit': 500,
            'sort': {'count': -1}
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64
        requests.push(url)

        let header
        if (window.bitdbApiKey) {
          header = {
            headers: { key: window.bitdbApiKey }
          }
        }

      let promises = [];
      for (let i = 0; i < requests.length; i++) {
          promises.push(axios.get(requests[i], header));
      }

      let chartData = {}
      axios.all(promises)
        .then(axios.spread((...args) => {
          for (let i = 0; i < args.length; i++) {
            for (let index = 0; index < args[i].data.c.length; ++index) {
              if (args[i].data.c[index]['_id'].length) {
                for (let j = 0; j < args[i].data.c[index]['_id'].length; ++j) {
                  if (args[i].data.c[index]['_id'][j].op === undefined) {
                    const b = new Buffer(args[i].data.c[index]['_id'][j], 'base64')
                    let hexId = b.toString('hex')
                    let itemName = hexId

                    if (chartData[itemName] !== undefined) {
                      chartData[itemName] = chartData[itemName] + args[i].data.c[index]['count']
                    } else {
                      chartData[itemName] = args[i].data.c[index]['count']
                    }
                  }
                }
              }
            }
          }
        }))
        .then(() => {
          let protocolChartData = {}
          for (let k in chartData){
            if (chartData.hasOwnProperty(k)) {
              const b = new Buffer(k, 'hex')
              let hexId = b.toString('hex')
              let itemId = b.toString('ascii')
              let itemName = ''
              if (protocols[hexId] !== undefined) {
                itemName = protocols[hexId].name
              } else if (hexId.length <= 8) {
                itemName = itemId + ' (' + hexId + ')'
              } else {
                itemName = itemId
              }

              // B protocol names population
              let prefix = itemId
              if (!(this.bitcomPrefixes.includes(prefix)) && protocols[prefix] === undefined) {
                try {
                  window.bchaddr.isLegacyAddress(prefix)
                  this.bitcomPrefixes.push(prefix)
                } catch (e) {
                }
              }

              if (protocolChartData[itemName] === undefined) {
                protocolChartData[itemName] = {}
                protocolChartData[itemName]['count'] = chartData[hexId]
                protocolChartData[itemName]['hex'] = []
                protocolChartData[itemName]['hex'].push(hexId)
              }
              if (protocolChartData[itemName] < chartData[hexId]) {
                protocolChartData[itemName]['count'] = chartData[hexId]
                if (!(protocolChartData[itemName]['hex'].includes(hexId))) {
                  protocolChartData[itemName]['hex'].push(hexId)
                }
              }
            }
          }
          this.getBitcomProtocols()

          for (let k in protocolChartData){
            if (protocolChartData.hasOwnProperty(k)) {
              this.chartData.push([k, protocolChartData[k]['count'], protocolChartData[k]['hex']])
            }
          }
          this.chartData.sort((a, b) => parseFloat(b[1]) - parseFloat(a[1]))
          this.chartDataFull = this.chartData
          this.chartData = this.chartData.slice(0, parseInt(this.itemsOnChart))

//          this.loading = false
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    },
    getActionsChartData () {
      let nowTimestamp = new Date().getTime() / 1000
      // remove this if use with for

      let requests = []
      for (let i = this.periodInput; i > 0; --i) {
        let beginTimestamp = nowTimestamp - (i * 24 * 60 * 60)
        let endTimestamp = nowTimestamp - ((i - 1) * 24 * 60 * 60)

        let queryMatch = {}
        queryMatch['$and'] = []
        queryMatch['$and'].push({'out.s1': { '$exists': true, '$ne': [] }})
        queryMatch['$and'].push({'out.s2': {'$ne': null}})
        queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

        let query = {
          'v': 3,
          'q': {
            'aggregate': [{
              '$match': queryMatch
            }, {
              '$group': {
  				      '_id': '$out.b1',
                'count': {
                  '$sum': 1
                }
              }
            }],
            'limit': 500,
            'sort': {'count': -1}
          }
        }

        let b64 = Base64.encode(JSON.stringify(query))
        let url = window.bitdbNode + b64
        requests.push(url)
      }

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      let promises = [];
      for (let i = 0; i < requests.length; i++) {
          promises.push(axios.get(requests[i], header));
      }

      let chartData = {}
      axios.all(promises)
        .then(axios.spread((...args) => {
          for (let i = 0; i < args.length; i++) {
            for (let index = 0; index < args[i].data.c.length; ++index) {
              if (args[i].data.c[index]['_id'].length) {
                for (let j = 0; j < args[i].data.c[index]['_id'].length; ++j) {
                  if (args[i].data.c[index]['_id'][j].op === undefined) {
                    const b = new Buffer(args[i].data.c[index]['_id'][j], 'base64')
                    let hexId = b.toString('hex')
                    let itemId = b.toString('ascii')
                    let itemName = ''
                    if (protocols[hexId] !== undefined) {
                      itemName = protocols[hexId].name
                    } else if (hexId.length <= 8) {
                      itemName = itemId + ' (' + hexId + ')'
                    } else {
                      itemName = itemId
                    }

                    if (chartData[itemName] !== undefined) {
                      chartData[itemName]['count'] = chartData[itemName]['count'] + args[i].data.c[index]['count']
                      if (!(chartData[itemName]['hex'].includes(hexId))) {
                        chartData[itemName]['hex'].push(hexId)
                      }
                    } else {
                      chartData[itemName] = {}
                      chartData[itemName]['count'] = args[i].data.c[index]['count']
                      chartData[itemName]['hex'] = []
                      chartData[itemName]['hex'].push(hexId)
                    }

                    // B protocol names population
                    let prefix = itemId
                    if (!(this.bitcomPrefixes.includes(prefix)) && protocols[prefix] === undefined) {
                      try {
                        window.bchaddr.isLegacyAddress(prefix)
                        this.bitcomPrefixes.push(prefix)
                      } catch (e) {
                      }
                    }
                  }
                }
              }
            }
          }

          this.getBitcomProtocols()
        }))
        .then(() => {
          for (var k in chartData){
            if (chartData.hasOwnProperty(k)) {
              this.chartData.push([k, chartData[k]['count'], chartData[k]['hex']])
            }
          }
          this.chartData.sort((a, b) => parseFloat(b[1]) - parseFloat(a[1]))
          this.chartDataFull = this.chartData
          this.chartData = this.chartData.slice(0, parseInt(this.itemsOnChart))

//          this.loading = false
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    },
    getBitcomProtocols () {
      let queryMatch = {}
      queryMatch['$and'] = []
      queryMatch['$and'].push({'out.s1': '$'})
      queryMatch['$and'].push({'out.s2': 'echo'})
      queryMatch['$and'].push({'out.s4': 'to'})
      queryMatch['$and'].push({'out.s5': {'$in': ['name']}})
      queryMatch['$and'].push({'in.e.a': {'$in': this.bitcomPrefixes}})

      let query = {
        'v': 3,
        'q': {
          'find': queryMatch,
          'skip': 0,
          'limit': 500
        }
      }
      let b64 = Base64.encode(JSON.stringify(query))
      let url = bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data !== undefined && (response.data.u !== undefined || response.data.c !== undefined)) {
            let bitcomEchoResults = []
            if (response.data.u !== undefined) {
              bitcomEchoResults = bitcomEchoResults.concat(response.data.u.reverse())
            }
            if (response.data.c !== undefined) {
              bitcomEchoResults = bitcomEchoResults.concat(response.data.c)
            }

            let bitcomProtocols = {}
            for (let i = 0; i < bitcomEchoResults.length; ++i) {
              for (let j = 0; j < bitcomEchoResults[i]['out'].length; ++j) {
                if (!bitcomEchoResults[i]['out'][j]) {
                  continue
                }
                if (bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a] === undefined) {
                  bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a] = {}
                }
                let field = bitcomEchoResults[i]['out'][j].s5
                if (bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a][field] === undefined) {
                  bitcomProtocols[bitcomEchoResults[i]['in'][0].e.a][field] = bitcomEchoResults[i]['out'][j].s3
                }
              }
            }

            for (let key in bitcomProtocols) {
              if (!bitcomProtocols.hasOwnProperty(key)) continue;

              for (let ii = 0; ii < this.chartData.length; ii++) {
                if (this.chartData[ii][0] === key) {
                  this.chartData[ii][0] = bitcomProtocols[key]['name']
                }
              }
            }
            this.loading = false
          }
        })
        .catch(error => {
          console.log(error)
          this.errored = false
        })
    },
    getTrendsLink (hexIds) {
      let query = 'prefix:' + hexIds.join(', prefix:')
      let b64 = Base64.encode(JSON.stringify(query).slice(1, JSON.stringify(query).length-1))
      return '/result/' + b64 + '/30'
    }
  }
}
</script>

<style>
</style>



