<template>
  <div class="grid">
    <HeaderNav :search="true" :searchText="searchTextInput" :searchPeriod="periodInput" v-on:searchTextInput="onSearch"/>

    <div class="s_results" v-if="loading === false">
      <p>
        <button @click="changePeriod(7)" v-bind:class="{ active: (periodInput == 7) }">7 days</button>
        <button @click="changePeriod(30)" v-bind:class="{ active: (periodInput == 30) }">30 days</button>
        <button @click="changePeriod(90)" v-bind:class="{ active: (periodInput == 90) }">90 days</button>
        <button @click="changePeriod(365)" v-bind:class="{ active: (periodInput == 365) }">1 year</button>
        <a :href="getOyoLink(searchTextInput)" style="margin-left:20px;">See on Oyo</a>
      </p>
      <line-chart :data="chartData" :curve="false" :messages="{empty: 'No data'}" :dataset="{borderWidth: 3, pointRadius: 0}" :library="{tooltips: {mode: 'index', intersect: false}, hover: {mode: 'nearest', intersect: true}}"></line-chart>

    </div>
    <div class="s_results" v-else>
      <div class="n_results">
        Loading...
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
import HeaderNav from '@/components/Header/Nav'
import Footer from '@/components/Footer'

export default {
  name: 'Result',
  props: {
    search: {type: String, required: true},
    period: {type: String, required: true}
  },
  data () {
    return {
      chartData: [],
      searchPhrases: [],
      searchTextInput: null,
      periodInput: null,
      periodFormat: null,
      loading: true,
      errored: false
    }
  },
  head: function () {
    return {
      title: {
        inner: 'Charts for mentions of "' + this.searchPhrases.join('", "') + '" on Bitcoin Cash'
      },
      // Meta tags
      meta: [
        { name: 'application-name', content: 'Trends.cash' },
        { name: 'description', content: 'Charts for mentions of "' + this.searchPhrases.join('", "') + '" on the Bitcoin Cash blockchain. Compare results for the last ' + this.periodInput + ' days', id: 'desc' },
        { name: 'twitter:title', content: 'Charts for mentions of "' + this.searchTextInput + '" on Bitcoin Cash' },
        { n: 'twitter:description', c: 'Charts for mentions of "' + this.searchPhrases.join('", "') + '" on the Bitcoin Cash blockchain. Compare results for the last ' + this.periodInput + ' days' },
        { itemprop: 'name', content: 'Charts for mentions of "' + this.searchTextInput + '" on Bitcoin Cash' },
        { itemprop: 'description', content: 'Charts for mentions of "' + this.searchPhrases.join('", "') + '" on the Bitcoin Cash blockchain. Compare results for the last ' + this.periodInput + ' days' },
        { property: 'og:title', content: 'Charts for mentions of "' + this.searchTextInput + '" on Bitcoin Cash' },
      ]
    }
  },
  components: {
    HeaderNav,
    Footer
  },
  mounted () {
    this.periodInput = parseInt(this.period)
    this.searchTextInput = Base64.decode(this.search)
    this.splitTextInput()
    this.populateChart()
  },
  watch: {
    searchPhrases: function (val) {
      this.$emit('updateHead')
    },
    periodInput: function (val) {
      this.$emit('updateHead')
    }
  },
  methods: {
    dateFormater (a, t) {
      let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
      let year = a.getFullYear()
      let month_text = months[a.getMonth()]
      let month = ('0' + (a.getMonth() + 1)).substr(-2)
      let date = ('0' + (a.getDate())).substr(-2)
      let hour = ('0' + a.getHours()).substr(-2)
      let min = ('0' + a.getMinutes()).substr(-2)
      if (t === 1) {
        return year + '-' + month + '-' + date
      }
      return date + ' ' + month + ' ' + year + ' ' + hour + ':' + min
    },
    splitTextInput () {
      let tmpString = this.searchTextInput.replace(/['"]+/g, '')
      this.searchPhrases = tmpString.split(/\s*,\s*/)
    },
    changePeriod (period) {
      this.periodInput = parseInt(period)
      this.changeSearchRoute()
    },
    changeSearchRoute () {
      if (this.searchTextInput) {
        this.$router.push('/result/' + Base64.encode(this.searchTextInput) + '/' + this.periodInput)
        this.splitTextInput()
        this.populateChart()
      }
    },
    populateChart () {
      this.loading = true
      this.chartData = []

      for (let pcIndex = 0; pcIndex < this.searchPhrases.length; ++pcIndex) {
        this.getMatchCountArray(this.searchPhrases[pcIndex])
      }
    },
    getMatchCountArray (searchPhrase) {
      let nowTimestamp = new Date().getTime() / 1000
      let beginTimestamp = parseInt(nowTimestamp - (this.periodInput * 24 * 60 * 60))
      let endTimestamp = parseInt(nowTimestamp)

      let periodFormat = '%Y-%m-%d'

      let matchPhrase = searchPhrase
      let queryMatch = {}
      queryMatch['$and'] = []
      while (matchPhrase.match(/s(?:[1-9]|1[0-5]):/)) {
        let match = matchPhrase.match(/(?:^|\s+)s([1-9]|1[0-5]):\s*(\S+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.s' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)s' + match[1] + ':\\s*\\S+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/h(?:[1-9]|1[0-5]):\s*[0-9a-fA-F]+/)) {
        let match = matchPhrase.match(/(?:^|\s+)h([1-9]|1[0-5]):\s*([0-9a-fA-F]+)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.h' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)h' + match[1] + ':\\s*[0-9a-fA-F]+\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      while (matchPhrase.match(/b(?:[1-9]|1[0-5]):\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?/)) {
        let match = matchPhrase.match(/(?:^|\s+)b([1-9]|1[0-5]):\s*((?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?)\s*/u)
        if (match && match[1] && match[2]) {
          var jsonVariable = {}
          jsonVariable['out.b' + match[1]] = match[2]
          queryMatch['$and'].push(jsonVariable)

          let regex = new RegExp( '(?:^|\\s+)b' + match[1] + ':\\s*(?:[A-Za-z0-9+/]{4})*(?:[A-Za-z0-9+/]{2}==|[A-Za-z0-9+/]{3}=)?\\s*', 'gu' )
          matchPhrase = matchPhrase.replace(regex, ' ')
        }
      }
      if (matchPhrase.match(/prefix:\s*[0-9a-fA-F]+\b/)) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-fA-F]+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'out.h1': match[1]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-fA-F]+\s*/gu, ' ')
        }
      }
      if (matchPhrase.match(/prefix:\s*[0-9a-zA-Z]+\b/)) {
        let match = matchPhrase.match(/(?:^|\s+)prefix:\s*([0-9a-zA-Z]+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'out.s1': match[1]})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)prefix:\s*[0-9a-zA-Z]+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("from:")) {
        let match = matchPhrase.match(/(?:^|\s+)from:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': window.convertAddress(match[1])})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)from:\s*\S+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("notfrom:")) {
        let match = matchPhrase.match(/(?:^|\s+)notfrom:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'in.e.a': { '$ne': window.convertAddress(match[1]) }})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)notfrom:\s*\S+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("to:")) {
        let match = matchPhrase.match(/(?:^|\s+)to:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'out.e.a': window.convertAddress(match[1])})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)to:\s*\S+\s*/gu, ' ')
        }
      }
      if (matchPhrase.includes("notto:")) {
        let match = matchPhrase.match(/(?:^|\s+)notto:\s*(\S+)\s*/u)
        if (match && match[1]) {
          queryMatch['$and'].push({'out.e.a': { '$ne': window.convertAddress(match[1]) }})
          matchPhrase = matchPhrase.replace(/(?:^|\s+)notto:\s*\S+\s*/gu, ' ')
        }
      }

      matchPhrase = matchPhrase.trim()

      if (matchPhrase) {
        queryMatch['$and'].push({'$text': {'$search': '\"' + matchPhrase + '\"'}})
      }
      queryMatch['$and'].push({'blk.t': {'$gte': beginTimestamp, '$lte': endTimestamp}})

      let query = {
        'v': 3,
        'q': {
          'aggregate': [{
            '$match': queryMatch
          }, {
            '$group': {
              '_id': {
                '$dateToString': {
                  'format': periodFormat,
                  'date': {
                    '$toDate': {
                      '$multiply': [1000, '$blk.t']
                    }
                  }
                }
              },
              'count': {
                '$sum': 1
              }
            }
          }],
          'limit': 10000,
          'sort': {'_id': 1}
        }
      }

      let b64 = Base64.encode(JSON.stringify(query))
      let url = window.bitdbNode + b64

      let header
      if (window.bitdbApiKey) {
        header = {
          headers: { key: window.bitdbApiKey }
        }
      }

      axios
        .get(url, header)
        .then(response => {
          if (response.data.c.length > 0) {
            let prevItemDate = null
            let singleChartData = []
            for (let index = 0; index < response.data.c.length; ++index) {
                let itemDate = new Date(response.data.c[index]['_id'])
                if (index === 0) {
                  // fill with 0 if it's not the beginning
                  let timeDiff = Math.abs(itemDate.getTime() - ((new Date()).getTime() - (this.periodInput * 24 * 60 * 60 * 1000)))
                  let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
                  if (diffDays === 1) {
                    diffDays = 0
                  }

                  if (diffDays > 0) {
                    for (let j = diffDays; j > 0; --j) {
                      let fillDate = new Date(itemDate)
                      fillDate.setDate(itemDate.getDate() - j)
                      singleChartData.push([this.dateFormater(fillDate, 1), 0])
                    }
                  }
                } else {
                  let timeDiff = Math.abs(itemDate.getTime() - prevItemDate.getTime())
                  let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
                  // fill empty dates with 0
                  if (diffDays > 1) {
                    for (let j = 1; j < diffDays; ++j) {
                      let fillDate = new Date(prevItemDate)
                      fillDate.setDate(prevItemDate.getDate() + j)
                      singleChartData.push([this.dateFormater(fillDate, 1), 0])
                    }
                  }
                }

                singleChartData.push([response.data.c[index]['_id'], response.data.c[index]['count']])
                prevItemDate = itemDate
            }

            // fill with 0 till the end
            let timeDiff = Math.abs((new Date()).getTime() - prevItemDate.getTime())
            let diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24))
            if (diffDays > 0) {
              for (let j = 1; j < diffDays; ++j) {
                let fillDate = new Date(prevItemDate)
                fillDate.setDate(prevItemDate.getDate() + j)
                singleChartData.push([this.dateFormater(fillDate, 1), 0])
              }
            }
            // push data to chart
            this.chartData.push({'name': searchPhrase, 'data': singleChartData})
          }
          this.loading = false
        })
        .catch(error => {
          console.log(error)
          this.errored = true
        })
    },
    onSearch: function (searchTextInput) {
      this.searchTextInput = searchTextInput
      this.changeSearchRoute()
    },
    getOyoLink (searchInput) {
      let b64 = Base64.encode(searchInput)
      return 'https://oyo.cash/result/' + b64 + '/0'
    }
  }
}
</script>

<style>
</style>



